.whole-card {
  position: relative;
  width: 165.5px;
  height: 110px;
  top: 0;
  left: 0;
}

.blank-card {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

/*Card Colors*/
.positive {
    -webkit-filter: saturate(200%) hue-rotate(95deg);
            filter: saturate(200%) hue-rotate(95deg);
}

.negative {
    -webkit-filter: saturate(200%) hue-rotate(315deg);
            filter: saturate(200%) hue-rotate(315deg);
}

.crit {
    -webkit-filter: saturate(200%) hue-rotate(270deg);
            filter: saturate(200%) hue-rotate(270deg);
}

.miss {
    -webkit-filter: saturate(200%) hue-rotate(20deg);
            filter: saturate(200%) hue-rotate(20deg);
}

.standard {

}

/*Card Extras*/
.Plus {
    position: absolute;
    top: 14px;
    left: 46px;
    height: 80%;
}

.Times {
    position: absolute;
    top: 41px;
    left: 61px;
    height: 33%;
}

.Minus {
    position: absolute;
    top: 51px;
    left: 60px;
    height: 16%;
}

.Zero {
    position: absolute;
    top: 29px;
    left: 89px;
    height: 50%;
}

.One {
    position: absolute;
    top: 24px;
    left: 80px;
    height: 60%;
}

.Two {
    position: absolute;
    top: 22px;
    left: 82px;
    height: 60%;
}

.Back {
    position: absolute;
    top: 1px;
    left: 7px;
    height: 100%;
}

.Curse {
    position: absolute;
    top: 13px;
    left: 22px;
    height: 76%;
}

.Bless {
    position: absolute;
    top: 16px;
    left: 24px;
    height: 70%;
}

.Miss {
    position: absolute;
    top: 23px;
    left: 58px;
    height: 60%;
}



